import './App.css';
import Editor from './component/Editor';
import {useState,useEffect} from 'react'
function App() {
  const[Html,setHtml] = useState('')
  const[css,setCss] = useState('')
  const[js,setjs] = useState('')
  const [srcDoc,setSrcDoc] = useState('')
  useEffect(() => {
  const timeout = setTimeout(()=>{
    setSrcDoc(
      `<html>
      <body>${Html}</html>
      <body>${css}</body>
      <script>${js}</script>
      `
    )
  },250) 
  return ()=> clearTimeout(timeout)
  }, [Html,css,js])
  return (
    <div className="App">
      <div className="pane top-pane">
      <Editor value={Html} onChange={setHtml} language="xml"displayName="HTML"/>
      <Editor value={css} onChange={setCss} language="css"displayName="CSS"/>
      <Editor value={js} onChange={setjs} language="javascript"displayName="JS"/>
      </div>
     <div className="pane">
      <iframe srcDoc={srcDoc} title="output"
      sandbox="allow-scripts"
    frameBorder="0"
      width="100%"
  height="100%"/> 
     </div>
    </div>
  );
}
export default App;
